import util from '../ons/util';
import autoStyle from '../ons/autostyle';
import ModifierUtil from '../ons/internal/modifier-util';
import BaseElement from '../ons/base-element';

const scheme = {'': 'button--*'};

const defaultClassName = 'button';

/**
 * @element ons-button
 * @category form
 * @modifier outline
 *   [en]Button with outline and transparent background[/en]
 *   [ja][/ja]
 * @modifier light
 *   [en]Button that doesn't stand out.[/en]
 *   [ja][/ja]
 * @modifier quiet
 *   [en]Button with no outline and or background..[/en]
 *   [ja][/ja]
 * @modifier cta
 *   [en]Button that really stands out.[/en]
 *   [ja][/ja]
 * @modifier large
 *   [en]Large button that covers the width of the screen.[/en]
 *   [ja][/ja]
 * @modifier large--quiet
 *   [en]Large quiet button.[/en]
 *   [ja]quiet[/ja]
 * @modifier large--cta
 *   [en]Large call to action button.[/en]
 *   [ja]cta[/ja]
 * @modifier material
 *   [en]Material Design button[/en]
 *   [ja][/ja]
 * @modifier material--flat
 *   [en]Material Design flat button[/en]
 *   [ja][/ja]
 * @description
 *   [en]
 *     Button component. If you want to place a button in a toolbar, use `<ons-toolbar-button>` or `<ons-back-button>` instead.
 *
 *     Will automatically display as a Material Design button with a ripple effect on Android.
 *   [/en]
 *   [ja]ons-toolbar-buttonons-back-button[/ja]
 * @codepen hLayx
 * @tutorial vanilla/Reference/button
 * @guide using-modifier [en]More details about the `modifier` attribute[/en][ja]modifier[/ja]
 * @guide cross-platform-styling [en]Information about cross platform styling[/en][ja]Information about cross platform styling[/ja]
 * @example
 * <ons-button modifier="large--cta">
 *   Tap Me
 * </ons-button>
 */

export default class ButtonElement extends BaseElement {

  /**
   * @attribute modifier
   * @type {String}
   * @description
   *  [en]The appearance of the button.[/en]
   *  [ja][/ja]
   */

  /**
   * @attribute ripple
   * @description
   *  [en]If this attribute is defined, the button will have a ripple effect.[/en]
   *  [ja][/ja]
   */

  /**
   * @attribute disabled
   * @description
   *   [en]Specify if button should be disabled.[/en]
   *   [ja][/ja]
   */

  init() {
    this._compile();
  }

  static get observedAttributes() {
    return ['modifier', 'ripple', 'class'];
  }

  attributeChangedCallback(name, last, current) {
    switch (name) {
      case 'class':
        if (!this.classList.contains(defaultClassName)) {
          this.className = defaultClassName + ' ' + current;
        }
        break;
      case 'modifier':
        ModifierUtil.onModifierChanged(last, current, this, scheme);
        break;
      case 'ripple':
        this._updateRipple();
    }
  }

  /**
   * @property disabled
   * @type {Boolean}
   * @description
   *   [en]Whether the button is disabled or not.[/en]
   *   [ja]`true`[/ja]
   */
  set disabled(value) {
    return util.toggleAttribute(this, 'disabled', value);
  }

  get disabled() {
    return this.hasAttribute('disabled');
  }

  _compile() {
    autoStyle.prepare(this);

    this.classList.add(defaultClassName);

    this._updateRipple();

    ModifierUtil.initModifier(this, scheme);
  }

  _updateRipple() {
    util.updateRipple(this);
  }
}

customElements.define('ons-button', ButtonElement);



